$social-size: 28px;

.social {
    list-style:none;
    padding: 0;
    &__item {
        display: inline-block;
        margin:5px;
    }
    &__link{
        text-align: center;
        display: inline-block;
        color: $light-gray;
        text-decoration: none;
        width: $social-size;
        height: $social-size;
        border: 2px solid $light-gray;
        border-radius: 50%;
        line-height: 28px;
    }
}