.slider {
  position: relative; }
  .slider__slide {
    min-height: 100px; }
  .slider__arrow {
    position: absolute;
    top: 0;
    bottom: 0;
    padding: 20px;
    background: none;
    border: none;
    color: #ffcc00;
    font-size: 2em; }
    .slider__arrow:hover, .slider__arrow:focus, .slider__arrow:active {
      transform: scale(1.1);
      cursor: pointer; }
    .slider__arrow--prev {
      left: 0; }
      .slider__arrow--prev i {
        display: inline-block;
        margin-left: -3px; }
    .slider__arrow--next {
      right: 0; }
      .slider__arrow--next i {
        display: inline-block;
        margin-right: -3px; }
  .slider__dots {
    padding: 40px 0;
    margin: 0;
    list-style: none;
    text-align: center; }
    .slider__dots li {
      display: inline-block; }
  .slider__dot {
    display: inline-block;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: #ffcc00;
    border: 1px solid #fff; }
    .slider__dot:hover, .slider__dot:focus, .slider__dot:active, .slider__dot--active {
      background-color: #fff;
      border-color: #ffcc00;
      cursor: pointer; }

.social {
  list-style: none;
  padding: 0; }
  .social__item {
    display: inline-block;
    margin: 5px; }
  .social__link {
    text-align: center;
    display: inline-block;
    color: #cccccc;
    text-decoration: none;
    width: 28px;
    height: 28px;
    border: 2px solid #cccccc;
    border-radius: 50%;
    line-height: 28px; }

html {
  font-size: 10px; }

body {
  font-family: Raleway, sans-serif;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.7;
  color: #333333;
  padding: 50px 0;
  background-color: #fff; }

.container {
  margin: 0 auto;
  max-width: 1170px;
  padding: 15px; }

a, button {
  transition: all .3s ease-in; }

a:hover, a:focus, a:active,
button:hover, button:focus, button:active {
  transition: all .3s ease-in; }

.head {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 50px; }
  .head__subtitle {
    font-family: Raleway, sans-serif;
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 1.3;
    color: #898f8f; }
  .head__title {
    font-family: Roboto, sans-serif;
    font-weight: 900;
    font-size: 6rem;
    line-height: 1.1;
    color: #ffcc00;
    margin-top: 0; }
  .head__icon svg {
    margin: 0 10px;
    fill: #ffcc00; }
  .head__icon:before, .head__icon:after {
    content: '';
    display: inline-block;
    width: 210px;
    border-top: 1px solid #ffcc00;
    vertical-align: middle;
    margin-top: -10px; }

.profile {
  display: flex; }
  .profile__section {
    width: 50%;
    padding: 80px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .profile__section + .profile__section {
      border-left: 1px solid #ffcc00; }
  .profile__img-wrap {
    border: 1px solid #ffcc00;
    font-size: 0;
    padding: 15px;
    position: relative; }
    .profile__img-wrap:before, .profile__img-wrap:after {
      content: '';
      width: calc(100% + 1px * 3);
      height: 50%;
      background-color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
    .profile__img-wrap:after {
      width: 50%;
      height: calc(100% + 1px * 3); }
  .profile__img {
    position: relative;
    z-index: 1;
    border: 1px solid #ffcc00;
    width: 100%; }
  .profile__title {
    font-family: Roboto, sans-serif;
    font-weight: 900;
    font-size: 3.6rem;
    line-height: 1;
    color: #666666;
    margin-bottom: 30px; }
    .profile__title:after {
      content: '';
      display: block;
      width: 100px;
      border-top: 1px solid #ffcc00;
      margin: 35px auto 0; }
  .profile__subtitle {
    font-size: 2.4rem;
    color: #666666; }
  .profile__text {
    margin-bottom: 25px; }
  .profile__link {
    text-transform: uppercase;
    font-family: "Open Sans", sans-serif;
    font-weight: 700;
    font-size: 1.4rem;
    line-height: 1;
    color: #666666;
    text-decoration: none;
    border: 1px solid #ffcc00;
    display: inline-block;
    padding: .5em 1.4em; }
